import { FC, useState } from "react";
import { useSelector } from "react-redux";
import { Line } from "react-chartjs-2";
import {
  Chart as ChartJS,
  Tooltip,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
} from "chart.js";

import { formatDateShort } from "shared/utils/date";
import { TTag } from "entities/tag/types";
import { selectItemsByIntervals } from "features/stats/stats";

import s from "./style.module.css";
import { Button } from "shared/ui/button";

ChartJS.register(
  Tooltip,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement
);

export const chartOptions = {
  responsive: true,
};

export type TTagStatsProps = {
  tag: TTag;
};

export const TagStats: FC<TTagStatsProps> = ({ tag }) => {
  const itemsByIntervals = useSelector(selectItemsByIntervals);
  const [last10, setLast10] = useState(false);

  const labels = itemsByIntervals.map((period) => {
    return formatDateShort(period.interval[0]);
  });

  const stats = itemsByIntervals.map((period) => {
    const { items } = period;
    const tagItems = items.filter((i) => i.tag === tag.id);
    const tagTotal = tagItems.reduce((total, item) => total + item.price, 0);

    return tagTotal;
  });

  const lastStats = stats.slice(-10);

  const chartData = {
    labels,
    datasets: [
      {
        data: last10 ? lastStats : stats,
      },
    ],
  };

  return (
    <div>
      {lastStats.length < stats.length && (
        <div className={s.Filters}>
          <Button size="s" invert={last10} onClick={() => setLast10(false)}>Все</Button>
          <Button size="s" invert={!last10} onClick={() => setLast10(true)}>Последние 10 периодов</Button>
        </div>
      )}
      <Line options={chartOptions} data={chartData} />
    </div>
  );
};
