import { FC } from "react";
import { useSelector } from "react-redux";

import { useAppDispatch } from "shared/store";
import { reverseArray } from "shared/utils/array";
import { TPeriod } from "entities/period/types";
import {
  selectActivePeriod,
  selectAllPeriods,
} from "features/periods/slice/selectors";
import { setActivePeriod } from "features/periods/slice/actions";
import { openPeriod } from "features/view/slice/actions";

import { Period } from "./period";

export type TPeriodsListProps = {
  onSelect?: () => void;
};

export const PeriodsList: FC<TPeriodsListProps> = ({ onSelect }) => {
  const dispatch = useAppDispatch();
  const periods = useSelector(selectAllPeriods);
  const activePeriod = useSelector(selectActivePeriod);

  const reversedPeriods = reverseArray(periods);

  const handleSelect = (period: TPeriod) => {
    dispatch(setActivePeriod(period));
    if (onSelect) onSelect();
  };

  const handleEdit = (period: TPeriod) => {
    dispatch(openPeriod(period.id));
  };

  return (
    <div>
      {reversedPeriods.map((period, i) => (
        <Period
          key={period.id}
          period={period}
          active={activePeriod?.id === period.id}
          onSelect={() => handleSelect(period)}
          onEdit={() => handleEdit(period)}
        />
      ))}
    </div>
  );
};
