import { useSelector } from "react-redux";

import {
  selectActivePeriodId,
  selectActiveTagId,
  selectActiveTagStatsId,
} from "features/view/slice/selectors";

import { TagModal } from "./tagModal";
import { TagStatsModal } from "./tagStatsModal";
import { PeriodModal } from "./periodModal";

export const Modals = () => {
  const selectedTagId = useSelector(selectActiveTagId);
  const selectedTagStatsId = useSelector(selectActiveTagStatsId);
  const selectedPeriodId = useSelector(selectActivePeriodId);

  return (
    <>
      {selectedTagId && <TagModal tagId={selectedTagId} />}
      {selectedTagStatsId && <TagStatsModal tagId={selectedTagStatsId} />}
      {selectedPeriodId && <PeriodModal periodId={selectedPeriodId} />}
    </>
  );
};
