import { createSlice, PayloadAction } from "@reduxjs/toolkit";

type TView = {
  tag: number | null;
  tagStats: number | null;
  period: number | null;
  periodStats: number | null;
};

const initialState: TView = {
  tag: null,
  tagStats: null,
  period: null,
  periodStats: null,
};

export const viewSlice = createSlice({
  name: "view",
  initialState,
  reducers: {
    openTag(state, action: PayloadAction<number>) {
      const id = action.payload;
      state.tag = id;
    },
    closeTag(state) {
      state.tag = null;
    },
    openTagStats(state, action: PayloadAction<number>) {
      const id = action.payload;
      state.tagStats = id;
    },
    closeTagStats(state) {
      state.tagStats = null;
    },
    openPeriod(state, action: PayloadAction<number>) {
      const id = action.payload;
      state.period = id;
    },
    closePeriod(state) {
      state.period = null;
    }
  },
});
