import {
  PropsWithChildren,
  ReactNode,
  forwardRef,
  useEffect,
  useRef,
} from "react";
import { createPortal } from "react-dom";

import { TModalRef } from "shared/types";
import { useModalState } from "shared/hooks/useModalState";
import { Mask } from "shared/ui/mask";
import { Button } from "shared/ui/button";
import { CloseIcon } from "shared/icons/close";

import s from "./style.module.css";

export type TModalProps = PropsWithChildren & {
  icon?: ReactNode;
  title?: string;
  size?: "m" | "l";
  onClose?: () => void;
};

export const Modal = forwardRef<TModalRef, TModalProps>(
  ({ icon, size, title, onClose, children }, ref) => {
    const { isOpen, close } = useModalState(ref);

    if (!isOpen) return null;

    const handleClose = () => {
      if (onClose) onClose();
      else close();
    };

    const classes = [s.Modal, size === "l" && s.Large].filter(Boolean);

    return createPortal(
      <div className={classes.join(" ")}>
        <Mask onClick={handleClose} />
        <div className={s.FrameWrapper}>
          <div className={s.Container}>
            <div className={s.Frame}>
              <div className={s.Header}>
                <div className={s.HeaderContainer}>
                  {icon && <div className={s.Icon}>{icon}</div>}
                  {title && <div className={s.Title}>{title}</div>}
                </div>
                <Button rect onClick={handleClose}>
                  <CloseIcon />
                </Button>
              </div>
              <div className={s.Content}>{children}</div>
            </div>
          </div>
        </div>
      </div>,
      document.getElementById("modals") as HTMLElement
    );
  }
);
