import { FC } from "react";
import { useSelector } from "react-redux";

import { Field } from "shared/ui/field";
import { Input } from "shared/ui/input";
import { Select } from "shared/ui/select";
import { selectWeightedTags } from "features/stats/weightedTags";

import { TLocalSubitem } from "./types";
import s from "./style.module.css";

export type TSubitemProps = {
  item: TLocalSubitem;
  showErrors: boolean;
  onChange: (item: TLocalSubitem) => void;
};

export const Subitem: FC<TSubitemProps> = ({ item, showErrors, onChange }) => {
  const tags = useSelector(selectWeightedTags);
  const priceError =
    showErrors && !item.price.trim() ? "Обязательное поле" : "";
  const tagError = showErrors && !item.tag ? "Обязательное поле" : "";

  return (
    <div>
      <Field label="Краткое описание" optional>
        <Input
          value={item.text}
          onChange={(v) => onChange({ ...item, text: v })}
        />
      </Field>

      <div className={s.Wrapper}>
        <Field label="Сумма" required className={s.Price} error={priceError}>
          <Input
            type="number"
            value={item.price}
            onChange={(v) => onChange({ ...item, price: v })}
          />
        </Field>

        <Field label="Тег" required className={s.Tag} error={tagError}>
          <Select
            value={item.tag}
            options={tags.map((tag) => ({
              id: tag.id.toString(),
              text: tag.name,
            }))}
            emptyOption
            onChange={(v) => onChange({ ...item, tag: v })}
          />
        </Field>
      </div>
    </div>
  );
};
