import { createSelector } from "@reduxjs/toolkit";

import { RootState } from "shared/store";
import type { TItem } from "entities/item/types";
import type { TInterval } from "entities/period/types";
import { selectAllItems } from "features/items/slice/selectors";
import { selectActiveInterval } from "features/periods/slice/selectors";

export const selectActiveItems = createSelector(
  [selectAllItems, selectActiveInterval],
  (items: TItem[], activePeriod) => {
    const [from, to] = activePeriod;

    return items.filter((item) => {
      if (from && item.createdAt < from) return false;
      if (to && item.createdAt >= to) return false;
      return true;
    });
  }
);

export const selectActiveFlatItems = createSelector(
  [selectActiveItems],
  (items) => {
    const result: TItem[] = [];

    items.forEach((item) => {
      const { price, subitems = [] } = item;
      const subitemsPrice = subitems.reduce((total, i) => total + i.price, 0);
      result.push({
        ...item,
        price: price - subitemsPrice,
      });
      result.push(...subitems);
    });
    return result;
  }
);

export const selectActiveItemsByTag = createSelector(
  [selectActiveFlatItems, (state: RootState, tagId: number) => tagId],
  (items, tagId) => items.filter((item) => item.tag === tagId)
);

export const selectItemsInInterval = createSelector(
  [selectAllItems, (state: RootState, interval: TInterval) => interval],
  (items: TItem[], interval: TInterval) => {
    const [from, to] = interval;

    return items.filter((item) => {
      if (from && item.createdAt < from) return false;
      if (to && item.createdAt >= to) return false;
      return true;
    });
  }
);
