import { useNavigate, useLocation } from "react-router-dom";

import { Container } from "shared/ui/container";
import { Button } from "shared/ui/button";
import { ListIcon } from "shared/icons/list";
import { StatsIcon } from "shared/icons/stats";
import { routes } from "shared/constants/routes";

import s from "./style.module.css";
import { SettingsIcon } from "shared/icons/settings";

export const Footer = () => {
  const { pathname } = useLocation();

  const navigate = useNavigate();

  const handleStats = () => {
    navigate(routes.stats);
  };

  const handleItems = () => {
    navigate(routes.root);
  };

  const handleSettings = () => {
    navigate(routes.settings);
  };

  return (
    <>
      <div className={s.Offset}></div>
      <footer className={s.Footer}>
        <Container className={s.Container}>
          <div className={s.Action}>
            <Button
              ghost
              block
              onClick={handleItems}
              disabled={pathname === routes.root}
            >
              <ListIcon />
            </Button>
          </div>
          <div className={s.Action}>
            <Button
              ghost
              block
              onClick={handleStats}
              disabled={pathname === routes.stats}
            >
              <StatsIcon />
            </Button>
          </div>
          <div className={s.Action}>
            <Button
              ghost
              block
              onClick={handleSettings}
              disabled={pathname === routes.settings}
            >
              <SettingsIcon />
            </Button>
          </div>
        </Container>
      </footer>
    </>
  );
};
