import { forwardRef, useImperativeHandle, useRef } from "react";

import { TModalRef } from "shared/types";
import { Modal } from "shared/ui/modal";
import {TNewPeriodRef } from "widgets/periods/types";
import { PeriodForm } from "../periodForm";

export const NewPeriod = forwardRef<TNewPeriodRef>((_, ref) => {
  const modalRef = useRef<TModalRef | null>(null);

  const handleSubmitPeriod = () => {
    modalRef.current?.close();
  };

  const handleCloseModal = () => {
    modalRef.current?.close();
  };

  useImperativeHandle(ref, () => {
    return {
      addPeriod: () => {
        modalRef.current?.open();
      },
    };
  });

  return (
    <>
      <Modal ref={modalRef} title="Начать период" onClose={handleCloseModal}>
        <PeriodForm onSubmit={handleSubmitPeriod} />
      </Modal>
    </>
  );
});
