import { FC, PropsWithChildren, useRef } from "react";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";

import { routes } from "shared/constants/routes";
import { PlusIcon } from "shared/icons/plus";
import { Modal } from "shared/ui/modal";
import { TModalRef } from "shared/types";
import { selectNotifications } from "features/notifier/slice/selectors";
import { Header } from "widgets/header";
import { Footer } from "widgets/footer";
import { ItemForm } from "widgets/items/ui/itemForm";

import s from "./style.module.css";
import { Modals } from "widgets/modals";
import { Toast } from "features/notifier/ui/toast";

export const Layout: FC<PropsWithChildren> = ({ children }) => {
  const navigate = useNavigate();

  const itemModalRef = useRef<TModalRef | null>(null);

  const handleAddItem = () => {
    itemModalRef.current?.open();
  };

  const handleItemSubmit = () => {
    itemModalRef.current?.close();
    navigate(routes.root);
  };

  return (
    <div>
      <div className={s.Page}>
        <Header />

        <main className={s.Content}>{children}</main>

        <button type="button" className={s.Add} onClick={handleAddItem}>
          <PlusIcon />
        </button>

        <Footer />
      </div>

      <Modal ref={itemModalRef} title="Новый чек">
        <ItemForm onSubmit={handleItemSubmit} />
      </Modal>

      <Modals />

      <Toast />
    </div>
  );
};
