import { TItem } from "./types";

export function getLastId(items: TItem[]) {
  return items.reduce((max, item) => {
    const { id, subitems = [] } = item;
    return Math.max(id, ...subitems.map((subitem) => subitem.id));
  }, 0);
}

export function formatSubitemsList(subitemsList: TItem[]): TItem[] {
  const formattedList: TItem[] = [];

  subitemsList.forEach((subitem) => {
    const { tag, text, price } = subitem;
    if (text) {
      formattedList.push(subitem);
      return;
    }

    const double = formattedList.find((i) => {
      return i.tag === tag && !i.text;
    });

    if (!double) {
      formattedList.push(subitem);
      return;
    }

    double.price += price;
  });

  return formattedList;
}
