import { createAsyncThunk } from "@reduxjs/toolkit";
import { notifierSlice } from "./slice";
import { NotificationStatuses } from "entities/notification/types";
import { RootState } from "shared/store";

export const { addMessage, removeMessage } = notifierSlice.actions;

export const addNotification = createAsyncThunk<
  void,
  { text: string; status?: NotificationStatuses }
>("notifier/addNotification", async ({ text, status }, thunkApi) => {
  await thunkApi.dispatch(
    addMessage({ text, status: status || NotificationStatuses.info })
  );
  const {
    notifier: { lastId },
  } = thunkApi.getState() as RootState;
  setTimeout(() => {
    thunkApi.dispatch(removeMessage(lastId));
  }, 3000);
});
