import { selectPeriodById } from "features/periods/slice/selectors";
import { closePeriod } from "features/view/slice/actions";
import { FC, useEffect, useRef } from "react";
import { useSelector } from "react-redux";

import { RootState, useAppDispatch } from "shared/store";
import { TModalRef } from "shared/types";
import { Modal } from "shared/ui/modal";
import { PeriodForm } from "widgets/periods/ui/periodForm";

type TPeriodModalProps = {
  periodId: number;
};

export const PeriodModal: FC<TPeriodModalProps> = ({ periodId }) => {
  const dispatch = useAppDispatch();

  const modalRef = useRef<TModalRef | null>(null);
  const selectedPeriod = useSelector((state: RootState) =>
    selectPeriodById(state, periodId)
  );

  const onCloseModal = () => {
    modalRef.current?.close();
    dispatch(closePeriod());
  };

  useEffect(() => {
    modalRef.current?.open();
  }, []);

  return (
    <Modal ref={modalRef} title="Период" onClose={onCloseModal}>
      <PeriodForm period={selectedPeriod} onSubmit={onCloseModal} />
    </Modal>
  );
};
