import { useSelector } from "react-redux";

import { Container } from "shared/ui/container";
import {
  selectActivePeriod,
  selectLastPeriodId,
} from "features/periods/slice/selectors";
import { selectActiveItems } from "features/stats/itemsInTime";
import { ItemsList } from "widgets/items/ui/itemsList";

export const ItemsPage = () => {
  const items = useSelector(selectActiveItems);
  const activePeriod = useSelector(selectActivePeriod);
  const lastPeriodId = useSelector(selectLastPeriodId);

  return (
    <div>
      <Container>
        <ItemsList items={items} disabled={activePeriod?.id !== lastPeriodId} />
      </Container>
    </div>
  );
};
