import { forwardRef, useImperativeHandle, useRef } from "react";

import { TModalRef } from "shared/types";
import { Button } from "shared/ui/button";
import { Panel } from "shared/ui/panel";

import { NewTag } from "widgets/tags/ui/newTag";
import { TagsList } from "widgets/tags/ui/tagsList";

import { TNewTagRef } from "../tags/types";

export const TagsPanel = forwardRef<TModalRef>((_, ref) => {
  const newTagRef = useRef<TNewTagRef | null>(null);
  const tagsPanelRef = useRef<TModalRef | null>(null);

  const handleAddTag = () => {
    newTagRef.current?.addTag();
  };

  useImperativeHandle(ref, () => {
    return {
      open: () => {
        tagsPanelRef.current?.open();
      },
      close: () => {
        tagsPanelRef.current?.close();
      },
      toggle: () => {
        tagsPanelRef.current?.toggle();
      },
    };
  });

  return (
    <>
      <Panel
        ref={tagsPanelRef}
        right
        action={
          <Button
            block
            size="l"
            onClick={handleAddTag}
          >
            Добавить новый тег
          </Button>
        }
      >
        <TagsList />
      </Panel>

      <NewTag ref={newTagRef} />
    </>
  );
});
