import { TItem } from "entities/item/types";
import { TTag } from "entities/tag/types";
import { TPeriod } from "entities/period/types";

import {
  APP_STORE_NAME,
  DB_NAME,
  DB_VERSION,
  ITEMS_STORE_NAME,
  PERIODS_STORE_NAME,
  TAGS_STORE_NAME,
} from "./constants";
import { setDb } from "./db";
import { getStore } from "./utils";
import { TSettings } from "entities/settings/types";

function getItems(): Promise<TItem[]> {
  return new Promise((resolve) => {
    const items = getStore(ITEMS_STORE_NAME);
    const request = items.getAll();
    request.onsuccess = function () {
      const result = request.result;
      resolve(result);
    };
  });
}

async function getTags(): Promise<TTag[]> {
  return new Promise((resolve) => {
    const tags = getStore(TAGS_STORE_NAME);
    const request = tags.getAll();
    request.onsuccess = function () {
      const result = request.result;
      resolve(result);
    };
  });
}

async function getPeriods(): Promise<TPeriod[]> {
  return new Promise((resolve) => {
    const periods = getStore(PERIODS_STORE_NAME);
    const request = periods.getAll();
    request.onsuccess = function () {
      const result = request.result;
      resolve(result);
    };
  });
}

async function getSettings(): Promise<TSettings> {
  return new Promise((resolve) => {
    const store = getStore(APP_STORE_NAME);
    const request = store.get("settings");
    request.onsuccess = function () {
      const result = request.result || {};
      resolve(result);
    };
  });
}

export async function getDBSnapshot(): Promise<{
  items: TItem[];
  tags: TTag[];
  periods: TPeriod[];
  settings: TSettings;
}> {
  return Promise.all([getTags(), getItems(), getPeriods(), getSettings()]).then(
    ([tags, items, periods, settings]) => {
      return { tags, items, periods, settings };
    }
  );
}

export async function initDB(): Promise<{
  items: TItem[];
  tags: TTag[];
  periods: TPeriod[];
  settings: TSettings;
}> {
  return new Promise((resolve) => {
    const dbRequest = indexedDB.open(DB_NAME, DB_VERSION);
    dbRequest.onupgradeneeded = function () {
      let db = dbRequest.result;
      if (!db.objectStoreNames.contains(ITEMS_STORE_NAME)) {
        db.createObjectStore(ITEMS_STORE_NAME, { keyPath: "id" });
      }
      if (!db.objectStoreNames.contains(TAGS_STORE_NAME)) {
        db.createObjectStore(TAGS_STORE_NAME, { keyPath: "id" });
      }
      if (!db.objectStoreNames.contains(PERIODS_STORE_NAME)) {
        db.createObjectStore(PERIODS_STORE_NAME, { keyPath: "id" });
      }
      if (!db.objectStoreNames.contains(APP_STORE_NAME)) {
        db.createObjectStore(APP_STORE_NAME);
      }
    };
    dbRequest.onsuccess = async function () {
      const data = await getDBSnapshot();
      resolve(data);
    };
    setDb(dbRequest);
  });
}
