import { useCallback } from "react";
import { useDispatch, useSelector } from "react-redux";

import { TNewItemData } from "entities/item/types";
import { selectSettings } from "features/settings/slice/selectors";

import { addItem, addItems } from "../slice/actions";

export function useAddItem() {
  const dispatch = useDispatch();
  const { detachSubitems } = useSelector(selectSettings);

  const addItemToStore = useCallback(
    (itemData: TNewItemData) => {
      if (detachSubitems) {
        const {
          subitems = [],
          price: itemPrice,
          text: itemText,
          ...originalItemData
        } = itemData;

        let subitemsTotalPrice = 0;

        const subitemsList = subitems?.map((subitem) => {
          const {
            text: subitemText,
            price: subitemPrice,
            ...subitemData
          } = subitem;
          subitemsTotalPrice += subitemPrice;

          return {
            text: subitemText || itemText || "",
            price: subitemPrice,
            subitems: [],
            ...subitemData,
          };
        });

        const updatedItemData = {
          ...originalItemData,
          text: itemText,
          subitems: [],
          price: itemPrice - subitemsTotalPrice,
        };

        const itemsData = [updatedItemData, ...subitemsList];
        dispatch(addItems(itemsData));
      } else {
        dispatch(addItem(itemData));
      }
    },
    [dispatch, detachSubitems]
  );

  return addItemToStore;
}
