export function _getDate(date: Date | number) {
  if (typeof date === "number") return new Date(date);
  return date;
}

export function _getDateComponents(date: Date | number | null) {
  if (!date) return null 
  const _date = _getDate(date);

  const day = _date.getDate()
  const month = _date.getMonth() + 1
  const fullYear = _date.getFullYear()

  return {
    day: day.toString().padStart(2, '0'),
    month: month.toString().padStart(2, '0'),
    fullYear: fullYear.toString(),
    shortYear: String(fullYear % 100)
  }
}

export function formatDate(date: Date | number | null) {
  const _date = _getDateComponents(date)
  if (!_date) return '...';
  return `${_date.day}.${_date.month}.${_date.fullYear}`;
}

export function formatDateShort(date: Date | number | null) {
  const _date = _getDateComponents(date)
  if (!_date) return '...';
  return `${_date.day}.${_date.month}.${_date.shortYear}`;
}

export function getDiff(date1: Date | number, date2?: Date | number | null) {
  if (!date2) date2 = new Date()
  const diff = Math.floor((+date2 - +date1) / 1000);
  const hours = diff / 60 / 60;
  const days = hours / 24;
  return Math.floor(days);
}