import { createSlice, PayloadAction } from "@reduxjs/toolkit";

type TUser = {
  uid: string;
  email: string | null;
};

type TProfileState = {
  user: TUser | null;
  inited: boolean;
};

const initialState: TProfileState = {
  user: null,
  inited: false,
};

export const profileSlice = createSlice({
  name: "profile",
  initialState,
  reducers: {
    setUser(state, action: PayloadAction<TUser | null>) {
      const user = action.payload;
      state.user = user;
      state.inited = true;
    },
  },
});
