import { FC, FormEventHandler, useCallback, useEffect, useState } from "react";
import { useSelector } from "react-redux";

import { RootState, useAppDispatch } from "shared/store";
import { Field } from "shared/ui/field";
import { Input } from "shared/ui/input";
import { Button } from "shared/ui/button";
import { TNewTagData, TTag } from "entities/tag/types";
import { selectAllTags } from "features/tags/slice/selectors";
import { addTag, removeTag, updateTag } from "features/tags/slice/actions";
import { selectItemsByTag } from "features/items/slice/selectors";
import { addNotification } from "features/notifier/slice/actions";
import { NotificationStatuses } from "entities/notification/types";

export type TTagFormProps = {
  tag?: TTag | null;
  onSubmit?: (tagName: string) => void;
};

export const TagForm: FC<TTagFormProps> = ({ tag, onSubmit }) => {
  const dispatch = useAppDispatch();
  const tags = useSelector(selectAllTags);
  const items = useSelector((state: RootState) =>
    selectItemsByTag(state, tag?.id)
  );

  const [tagName, setTagName] = useState("");
  const [tagLimit, setTagLimit] = useState("");
  const [error, setError] = useState("");

  const reset = useCallback(() => {
    setTagName("");
    setTagLimit("");
  }, []);

  const handleSubmit: FormEventHandler = (e) => {
    e.preventDefault();

    const formattedTagName = tagName.trim();

    if (!formattedTagName) {
      setError("Обязательное поле");
      return;
    }

    if (
      tags.some((t) => {
        if (tag?.id === t.id) return false;
        return t.name === tagName;
      })
    ) {
      setError("Тег с таким именем уже существует");
      return;
    }

    const tagData: TNewTagData = {
      name: tagName,
      limit: Number(tagLimit) || 0,
    };

    if (tag) {
      dispatch(updateTag({ tag, data: tagData }));
    } else {
      dispatch(addTag(tagData));
    }

    reset();

    if (onSubmit) onSubmit(tagName);
  };

  const handleRemove = () => {
    if (!tag) return;
    if (items.length) {
      dispatch(addNotification({ text: "Тег нельзя удалить", status: NotificationStatuses.error }))
      return;
    }
    dispatch(removeTag(tag));
  };

 

  useEffect(() => {
    setError("");
  }, [tagName]);

  useEffect(() => {
    if (!tag) {
      reset();
      return;
    }

    setTagName(tag.name);
    setTagLimit(tag.limit?.toString() || "");
  }, [tag, reset]);

  return (
    <form onSubmit={handleSubmit}>
      <Field label="Имя тега" required error={error}>
        <Input value={tagName} placeholder="Вкусняшки" onChange={setTagName} />
      </Field>

      <Field label="Лимит по тегу" optional>
        <Input
          type="number"
          value={tagLimit}
          placeholder="10000"
          onChange={setTagLimit}
        />
      </Field>

      <Button size="l" block type="submit">
        Сохранить тег
      </Button>

      {tag && (
        <>
          <Button size="l" invert block type="button" onClick={handleRemove}>
            Удалить тег
          </Button>
        </>
      )}
    </form>
  );
};
