import { forwardRef, useImperativeHandle, useRef, useState } from "react";

import { TModalRef } from "shared/types";
import { Modal } from "shared/ui/modal";

type TConfirmCallback = () => void;

type TConfirmConfig = {
  title?: string;
  text?: string;
  callback: TConfirmCallback;
};

export type TConfirmRef = { confirm: (config: TConfirmConfig) => void };

type TConfirmProps = {
  title?: string;
  text?: string;
};

export const Confirm = forwardRef<TConfirmRef, TConfirmProps>(
  ({ title: initialTitle = "", text: initialText = "" }, ref) => {
    const modalRef = useRef<TModalRef | null>(null);
    const [modalTitle, setModalTitle] = useState(initialTitle);
    const [modalText, setModalText] = useState(initialText);
    const confirmCallback = useRef<TConfirmCallback | null>(null);

    const openModal = () => {
      modalRef.current?.open();
    };

    const closeModal = () => {
      modalRef.current?.close();
    };

    const onConfirm = () => {
      closeModal();
      confirmCallback.current?.();
    };

    const confirm = ({ title = "", text = "", callback }: TConfirmConfig) => {
      setModalTitle(title);
      setModalText(text);
      confirmCallback.current = callback;
      openModal();
    };

    useImperativeHandle(ref, () => {
      return {
        confirm,
      };
    });

    return (
      <Modal ref={modalRef}>
        <div>
          <h2>{modalTitle}</h2>
          <p>{modalText}</p>
          <button onClick={onConfirm}>Confirm</button>
        </div>
      </Modal>
    );
  }
);
