import { FC, useEffect, useRef } from "react";
import { useSelector } from "react-redux";

import { RootState, useAppDispatch } from "shared/store";
import { TModalRef } from "shared/types";
import { Modal } from "shared/ui/modal";
import { selectTagById } from "features/tags/slice/selectors";
import { closeTag, openTagStats } from "features/view/slice/actions";
import { TagForm } from "widgets/tags/ui/tagForm";
import { Button } from "shared/ui/button";
import { StatsIcon } from "shared/icons/stats";

type TTagModalProps = {
  tagId: number;
};
export const TagModal: FC<TTagModalProps> = ({ tagId }) => {
  const dispatch = useAppDispatch();

  const modalRef = useRef<TModalRef | null>(null);
  const selectedTag = useSelector((state: RootState) =>
    selectTagById(state, tagId)
  );

  const onCloseModal = () => {
    modalRef.current?.close();
    dispatch(closeTag());
  };

  const handleOpenStats = () => {
    dispatch(openTagStats(tagId));
  };

  useEffect(() => {
    modalRef.current?.open();
  }, []);

  return (
    <Modal
      ref={modalRef}
      title='Тег'
      icon={
        <Button size="m" invert block type="button" onClick={handleOpenStats}>
          <StatsIcon />
        </Button>
      }
      onClose={onCloseModal}
    >
      <TagForm tag={selectedTag} onSubmit={onCloseModal} />
    </Modal>
  );
};
