import { useRef } from "react";
import { useSelector } from "react-redux";

import { Container } from "shared/ui/container";
import { TModalRef } from "shared/types";
import { CalendarIcon } from "shared/icons/calendar";
import { Button } from "shared/ui/button";
import { TagIcon } from "shared/icons/tag";
import { formatInterval } from "entities/period/utils";
import {
  selectActiveInterval,
  selectActivePeriod,
  selectLastPeriodId,
} from "features/periods/slice/selectors";
import { TagsPanel } from "widgets/tagsPanel";

import s from "./style.module.css";
import { PeriodsPanel } from "widgets/periodsPanel";
import { Link } from "react-router-dom";
import { UserIcon } from "shared/icons/user";
import { routes } from "shared/constants/routes";

export const Header = () => {
  const interval = useSelector(selectActiveInterval);
  const activePeriod = useSelector(selectActivePeriod);
  const lastPeriodId = useSelector(selectLastPeriodId);

  const periodsPanelRef = useRef<TModalRef | null>(null);
  const tagsPanelRef = useRef<TModalRef | null>(null);

  const subtitle =
    activePeriod?.id === lastPeriodId ? "Текущий период" : "Период завершен";

  const handleTogglePeriods = () => {
    periodsPanelRef.current?.toggle();
  };

  const handleToggleTags = () => {
    tagsPanelRef.current?.toggle();
  };

  return (
    <>
      <header className={s.Header}>
        <Container className={s.Container}>
          <div className={s.Left} onClick={handleTogglePeriods}>
            <CalendarIcon />

            <div>
              <div className={s.Title}>{formatInterval(interval)}</div>
              <div className={s.Subtitle}>{subtitle}</div>
            </div>
          </div>
          
          <div className={s.Right}>
            <Button ghost block onClick={handleToggleTags}>
              <TagIcon />
            </Button>
          </div>
        </Container>
      </header>

      <TagsPanel ref={tagsPanelRef} />
      <PeriodsPanel ref={periodsPanelRef} />
    </>
  );
};
