import { PropsWithChildren, ReactNode, forwardRef } from "react";
import { createPortal } from "react-dom";

import { useModalState } from "shared/hooks/useModalState";
import { Mask } from "shared/ui/mask";
import { Button } from "shared/ui/button";
import { CloseIcon } from "shared/icons/close";
import { TModalRef } from "shared/types";

import s from "./style.module.css";

export type TPanelProps = PropsWithChildren & {
  right?: boolean;
  action?: ReactNode;
};

export const Panel = forwardRef<TModalRef, TPanelProps>(
  ({ right, action, children }, ref) => {
    const { isOpen, close } = useModalState(ref);

    const classes = [
      s.Panel,
      right ? s.Right : s.Left,
      isOpen ? s.Active : "",
    ].join(" ");

    return createPortal(
      <>
        {isOpen && <Mask onClick={close} />}

        <aside className={classes}>
          <div className={s.Wrapper}>
            <div className={s.Header}>
              <Button ghost rect onClick={close}>
                <CloseIcon />
              </Button>
            </div>

            {action && <div className={s.Action}>{action}</div>}

            <div className={s.Container}>{children}</div>
          </div>
        </aside>
      </>,
      document.getElementById("panels") as HTMLElement
    );
  }
);
