import { FC, useEffect, useRef } from "react";
import { useSelector } from "react-redux";

import { RootState, useAppDispatch } from "shared/store";
import { TModalRef } from "shared/types";
import { Modal } from "shared/ui/modal";
import { selectTagById } from "features/tags/slice/selectors";
import { closeTagStats } from "features/view/slice/actions";
import { TagStats } from "widgets/stats/ui/tagStats";

type TTagStatsModalProps = {
  tagId: number;
};
export const TagStatsModal: FC<TTagStatsModalProps> = ({ tagId }) => {
  const dispatch = useAppDispatch();

  const modalRef = useRef<TModalRef | null>(null);
  const selectedTag = useSelector((state: RootState) =>
    selectTagById(state, tagId)
  );

  const onCloseModal = () => {
    modalRef.current?.close();
    dispatch(closeTagStats());
  };

  useEffect(() => {
    modalRef.current?.open();
  }, []);

  return (
    <Modal ref={modalRef} title={`Статистика по тегу ${selectedTag?.name}`} size="l" onClose={onCloseModal}>
      {selectedTag && <TagStats tag={selectedTag} />}
    </Modal>
  );
};
