import { FC, PropsWithChildren } from "react";

import s from "./style.module.css";

export type TFieldProps = PropsWithChildren & {
  className?: string;
  label?: string;
  error?: string;
  optional?: boolean;
  required?: boolean;
};

export const Field: FC<TFieldProps> = ({
  className,
  label,
  error,
  optional,
  required,
  children,
}) => {
  const renderHeader = () => {
    if (!label && !optional) return null;

    const labelClasses = [s.Label];
    if (required) labelClasses.push(s.Required);

    return (
      <div className={s.Header}>
        {label && <div className={labelClasses.join(" ")}>{label}</div>}
        {optional && <div className={s.Optional}>необязательно</div>}
      </div>
    );
  };

  return (
    <div className={[s.Field, className].join(" ")}>
      {renderHeader()}
      <div>{children}</div>
      {error && <div className={s.Error}>{error}</div>}
    </div>
  );
};
