import {
  ForwardedRef,
  useCallback,
  useEffect,
  useImperativeHandle,
  useRef,
  useState,
} from "react";

import { TModalRef } from "shared/types";
import { getUnique } from "shared/utils/unique";

const modals = new Set<number>();

function closeModal(mId: number) {
  modals.delete(mId);

  if (modals.size === 0) {
    delete document.body.dataset.modals;
  }
}

function openModal(mId: number) {
  modals.add(mId);

  document.body.dataset.modals = "true";

  return () => closeModal(mId);
}

export function useModalState(ref: ForwardedRef<TModalRef>) {
  const [isOpen, setIsOpen] = useState(false);
  const id = useRef(getUnique());

  const open = useCallback(() => {
    setIsOpen(true);
  }, []);
  const close = useCallback(() => {
    setIsOpen(false);
  }, []);
  const toggle = useCallback(() => {
    setIsOpen((prev) => !prev);
  }, []);

  useEffect(() => {
    const modalId = id.current;
    if (isOpen) openModal(modalId);
    else closeModal(modalId);

    return () => closeModal(modalId);
  }, [isOpen]);

  useImperativeHandle(ref, () => ({
    open,
    close,
    toggle,
  }));

  return { isOpen, open, close, toggle };
}
