import { FC, FormEventHandler, useState } from "react";
import { useSelector } from "react-redux";

import { useAppDispatch } from "shared/store";
import { Field } from "shared/ui/field";
import { Input } from "shared/ui/input";
import { Button } from "shared/ui/button";
import { TNewPeriodData, TPeriod } from "entities/period/types";
import { selectActivePeriod, selectAllPeriods } from "features/periods/slice/selectors";
import {
  addPeriod,
  setActivePeriod,
  updatePeriod,
} from "features/periods/slice/actions";

import s from "./style.module.css";

export type TPeriodFormProps = {
  period?: TPeriod | null;
  onSubmit?: () => void;
};

export const PeriodForm: FC<TPeriodFormProps> = ({ period, onSubmit }) => {
  const dispatch = useAppDispatch();
  const activePeriod = useSelector(selectActivePeriod);
  const periods = useSelector(selectAllPeriods);
  const lastPeriod = periods[periods.length - 1];

  const [periodName, setPeriodName] = useState(period ? period.name : "");

  const reset = () => {
    setPeriodName(period ? period.name : "");
  };

  const handleSubmit: FormEventHandler = (e) => {
    e.preventDefault();

    const updatedPeriod: TNewPeriodData = {
      name: periodName,
    };

    if (period) {
      dispatch(
        updatePeriod({
          period: period,
          data: updatedPeriod,
        })
      );
    } else {
      dispatch(addPeriod(updatedPeriod));
    }

    reset();

    if (onSubmit) onSubmit();
  };

  const handleActivate = () => {
    if (!period) return;
    dispatch(setActivePeriod(period));
    if (onSubmit) onSubmit();
  };

  return (
    <form onSubmit={handleSubmit}>
      {!period && lastPeriod && (
        <div className={s.Text}>Текущий период будет закрыт</div>
      )}

      {period && activePeriod?.id === period.id && (<div className={s.ActiveText}>Активный период</div>)}

      <Field label="Название периода" optional>
        <Input
          value={periodName}
          placeholder="Январь 2025"
          onChange={setPeriodName}
        />
      </Field>

      <Button size="l" block type="submit">
        {period ? "Сохранить" : "Начать новый период"}
      </Button>

      {period && activePeriod?.id !== period.id && (
        <Button size="l" block invert type="button" onClick={handleActivate}>
          Сделать активным
        </Button>
      )}
    </form>
  );
};
