import {
  createSlice,
  createEntityAdapter,
  PayloadAction,
} from "@reduxjs/toolkit";

import type { TItem, TNewItemData } from "entities/item/types";
import { formatSubitemsList, getLastId } from "entities/item/utils";
import { Progress } from "shared/constants/progress";
import {
  addItemToDb,
  addItemsToDb,
  removeItemFromDb,
  updateItemInDb,
} from "db";

export const itemsAdapter = createEntityAdapter<TItem>({
  sortComparer: (a, b) => b.id - a.id,
});

const initialState = itemsAdapter.getInitialState({
  status: Progress.idle,
  lastId: 0,
});

export const itemsSlice = createSlice({
  name: "items",
  initialState,
  reducers: {
    setItems(state, action: PayloadAction<TItem[]>) {
      const items = action.payload;
      itemsAdapter.removeAll(state)
      itemsAdapter.setAll(state, items);
      state.status = Progress.success;
      const lastId = getLastId(items);
      if (items.length) state.lastId = lastId;
    },
    addItem(state, action: PayloadAction<TNewItemData>) {
      state.lastId = state.lastId + 1;
      const createdAt = +new Date();

      const { subitems, ...itemData } = action.payload;

      const itemSubitemsList: TItem[] = subitems
        ? subitems.map((subitemData) => {
            state.lastId = state.lastId + 1;
            const { text, price, tag } = subitemData;

            const subitem: TItem = {
              id: state.lastId,
              createdAt,
              text,
              price,
              tag,
            };

            return subitem;
          })
        : [];

      const subitemsList = formatSubitemsList(itemSubitemsList);

      const item: TItem = {
        id: state.lastId,
        createdAt,
        ...itemData,
        subitems: subitemsList,
      };
      itemsAdapter.addOne(state, item);
      addItemToDb(item);
    },
    addItems(state, action: PayloadAction<TNewItemData[]>) {
      const createdAt = +new Date();
      const itemsList = action.payload;
      const items = itemsList.map((itemData) => {
        state.lastId = state.lastId + 1;
        const item: TItem = {
          id: state.lastId,
          createdAt,
          ...itemData,
          subitems: [],
        };
        return item;
      });
      itemsAdapter.addMany(state, items);
      addItemsToDb(items);
    },
    updateItem(
      state,
      action: PayloadAction<{ item: TItem; data: TNewItemData }>
    ) {
      const { item, data } = action.payload;
      const { subitems = [], ...fields } = data;

      const createdAt = item.createdAt;

      const updatedItem: TItem = {
        ...item,
        ...fields,
        subitems: subitems.map((subitemData) => {
          state.lastId = state.lastId + 1;
          const { text, price, tag } = subitemData;

          const subitem: TItem = {
            id: state.lastId,
            createdAt,
            text,
            price,
            tag,
          };

          return subitem;
        }),
      };

      itemsAdapter.updateOne(state, {
        id: item.id,
        changes: updatedItem,
      });

      updateItemInDb(updatedItem);
    },
    removeItem(state, action: PayloadAction<TItem>) {
      itemsAdapter.removeOne(state, action.payload.id);
      removeItemFromDb(action.payload);
    },
  },
});
