import { createSelector } from "@reduxjs/toolkit";

import { selectFlatItems } from "features/items/slice/selectors";
import { selectAllTags } from "features/tags/slice/selectors";

export const selectTagsWeight = createSelector(
  [selectAllTags, selectFlatItems],
  (tags, items) => {
    const weight: Record<number, number> = {};
    tags.forEach((tag) => {
      weight[tag.id] = 0;
    });
    items.forEach((item) => {
      weight[item.tag] += 1;
    });
    return weight;
  }
);

export const selectWeightedTags = createSelector(
  [selectAllTags, selectTagsWeight],
  (tags, weight) => {
    tags.sort((a, b) => weight[b.id] - weight[a.id]);
    return tags;
  }
);
