import { useState } from "react";
import { useSelector } from "react-redux";

import { useAppDispatch } from "shared/store";
import { login, logout, signUp } from "features/profile/slice/actions";
import { saveData, updateData } from "features/sync/slice/actions";
import { selectUser } from "features/profile/slice/selectors";

import s from "./style.module.css";
import { Button } from "shared/ui/button";
import { Input } from "shared/ui/input";

export const SyncForm = () => {
  const dispatch = useAppDispatch();
  const user = useSelector(selectUser);

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const handleSignUp = () => {
    if (!email || !password) return;
    dispatch(signUp({ email, password }));
  };

  const handleLogin = () => {
    if (!email || !password) return;
    dispatch(login({ email, password }));
  };

  const handleLogout = () => {
    dispatch(logout());
  };

  const handleSaveData = async () => {
    dispatch(saveData());
  };

  const handleUpdateData = async () => {
    dispatch(updateData());
  };

  if (user) {
    return (
      <div>
        <div className={s.Status}>
          <span>
            Вы авторизованы как <b>{user.email}</b>
          </span>
          &nbsp;
          <Button size="s" invert onClick={handleLogout}>
            Сменить аккаунт
          </Button>
        </div>

        <div className={s.Actions}>
          <Button onClick={handleSaveData}>Сохранить данные на сервере</Button>
          <Button onClick={handleUpdateData}>Обновить данные с сервера</Button>
        </div>
      </div>
    );
  }

  return (
    <div>
      <div className={s.Status}>Вы не авторизованы</div>
      <form>
        <div className={s.Inputs}>
            <Input value={email} onChange={setEmail} placeholder="Email" />
            <Input value={password} onChange={setPassword} placeholder="Password" />
        </div>

        <div className={s.Actions}>
          <Button onClick={handleLogin}>Войти</Button>
          <Button onClick={handleSignUp}>Зарегистрироваться</Button>
        </div>
      </form>
    </div>
  );
};
