import { getDb } from "./db";

export function getTransaction(storeName: string, toWrite: boolean = false) {
  let db = getDb();

  return db.transaction(
    storeName,
    toWrite ? "readwrite" : "readonly"
  );
}

export function getStore(storeName: string, toWrite: boolean = false) {
  const transaction = getTransaction(storeName, toWrite)
  const store = transaction.objectStore(storeName);
  return store;
}
