import { FC, PropsWithChildren } from "react";
import s from './style.module.css'

type TSetting = PropsWithChildren & {
  isChecked: boolean;
  onChange: (isChecked: boolean) => void;
};

export const Setting: FC<TSetting> = ({ isChecked, children, onChange }) => {
  return (
    <label className={s.Setting}>
      <input
        type="checkbox"
        checked={isChecked}
        onChange={(e) => onChange(e.target.checked)}
      />
      <span></span>

      {children}
    </label>
  );
};
