import { FC, useRef } from "react";

import { TModalRef } from "shared/types";
import { Button } from "shared/ui/button";
import { EyeOpen } from "shared/icons/eye-open";
import { EyeClose } from "shared/icons/eye-close";
import { ListIcon } from "shared/icons/list";
import { TTag } from "entities/tag/types";
import { TStatsItem } from "entities/stats/types";

import { StatsItemsList } from "../statsItemsList";

import s from "./style.module.css";

export type TTagStatsProps = {
  tag: TTag;
  items: TStatsItem[];
  total: number;
  active: boolean;
  onToggle: () => void;
};

export const TagStats: FC<TTagStatsProps> = ({
  tag,
  items,
  total,
  active,
  onToggle,
}) => {
  const listRef = useRef<TModalRef | null>(null);

  const handleToggle = () => {
    onToggle();
  };

  const handleShowList = () => {
    listRef.current?.open();
  };

  const sortedItems = [...items];
  sortedItems.sort((a, b) => b.createdAt - a.createdAt);

  return (
    <>
      <tr className={active ? "" : s.Inactive}>
        <td data-action>
          <Button ghost rect className={s.Action} onClick={handleToggle}>
            {active ? (
              <EyeOpen width={24} height={24} />
            ) : (
              <EyeClose width={24} height={24} />
            )}
          </Button>
        </td>
        <td data-name>
          <b>{tag.name}</b>
        </td>
        <td data-price>
          <b>{total}</b>
        </td>
        <td data-action>
          {active && (
            <Button ghost rect className={s.Action} onClick={handleShowList}>
              <ListIcon width={24} height={24} />
            </Button>
          )}
        </td>
      </tr>

      <StatsItemsList ref={listRef} tag={tag} items={sortedItems} />
    </>
  );
};
