import { initializeApp } from "firebase/app";
import {
  getDatabase,
  ref,
  update,
  onValue,
  DataSnapshot,
} from "firebase/database";
import {
  getAuth,
  onAuthStateChanged,
  createUserWithEmailAndPassword,
  signInWithEmailAndPassword,
  signOut,
  User,
} from "firebase/auth";
import { firebaseConfig } from "./config";

const app = initializeApp(firebaseConfig);
const auth = getAuth(app);
const db = getDatabase(app);
const userDataRef = ref(db, "userdata");

let currentUser: User | null = null;
let lastSnapshot: DataSnapshot | null = null;

const authCbs: Array<(user: User | null) => void> = [];

onAuthStateChanged(auth, (user) => {
  currentUser = user;
  authCbs.forEach((cb) => cb(currentUser));
});

onValue(userDataRef, (snapshot) => {
  lastSnapshot = snapshot;
  // if (snapshot.exists()) {
  //   console.log({ snapshot: Object.entries(snapshot.val()) });
  // } else {
  //   console.log("no snapshot");
  // }
});

export const subscribe = (cb: (user: User | null) => void) => {
  authCbs.push(cb);
};

export const signUp = (email: string, password: string) => {
  return createUserWithEmailAndPassword(auth, email, password)
    .then((userCredential) => {
      currentUser = userCredential.user;
      console.log(currentUser);
    })
    .catch((error) => {
      const errorCode = error.code;
      const errorMessage = error.message;
      console.log(errorCode, errorMessage);
    });
};

export const login = (email: string, password: string) => {
  return signInWithEmailAndPassword(auth, email, password)
    .then((userCredential) => {
      currentUser = userCredential.user;
      console.log(currentUser);
    })
    .catch((error) => {
      const errorCode = error.code;
      const errorMessage = error.message;
      console.log(errorCode, errorMessage);
    });
};

export const logout = () => {
  return signOut(auth)
    .then(() => {
      console.log("signed out");
    })
    .catch((error) => {
      console.log("error while signing out");
    });
};

export const getUser = () => {
  return currentUser;
};

export const saveUserData = async (userData: any) => {
  if (!currentUser) throw new Error("Пользователь не авторизован");

  return update(userDataRef, {
    [currentUser.uid]: userData,
  });
};

export const getUserData = () => {
  if (!currentUser) throw new Error("Пользователь не авторизован");
  if (lastSnapshot?.exists()) {
    const data = lastSnapshot.val();
    return data[currentUser.uid];
  }

  return null;
};
