import { FC } from "react";
import { useSelector } from "react-redux";

import { CloseIcon } from "shared/icons/close";
import { Button } from "shared/ui/button";
import { TPeriod } from "entities/period/types";
import { formatInterval } from "entities/period/utils";
import { selectPeriodInterval } from "features/periods/slice/selectors";

import s from "./style.module.css";
import { PencilIcon } from "shared/icons/pencil";

export type TPeriodProps = {
  period: TPeriod;
  active?: boolean;
  onSelect: (period: TPeriod) => void;
  onEdit: (period: TPeriod) => void;
};

export const Period: FC<TPeriodProps> = ({
  period,
  active,
  onSelect,
  onEdit,
}) => {
  const interval = useSelector((state) => selectPeriodInterval(state, period));
  const classes = [s.Period, active ? s.Active : ""].join(" ");

  return (
    <div className={classes}>
      <div className={s.Container}>
        <div className={s.Content} onClick={() => onSelect(period)}>
          <div className={s.Name}>{period.name}</div>

          <div className={s.Interval}>{formatInterval(interval)}</div>
        </div>

        <div className={s.Close}>
          <Button rect ghost onClick={() => onEdit(period)}>
            <PencilIcon />
          </Button>
        </div>
      </div>
    </div>
  );
};
