import { createAsyncThunk } from "@reduxjs/toolkit";

import { getUserData, saveUserData } from "shared/firebase";
import {
  getDBSnapshot,
  updateItemsInDb,
  updatePeriodsInDb,
  updateSettingsInDb,
  updateTagsInDb,
} from "db";
import { addNotification } from "features/notifier/slice/actions";
import { NotificationStatuses } from "entities/notification/types";
import { setItems } from "features/items/slice/actions";
import { setTags } from "features/tags/slice/actions";
import { setPeriods } from "features/periods/slice/actions";
import { setSettings } from "features/settings/slice/actions";

export const saveData = createAsyncThunk(
  "sync/saveData",
  async (_, thunkApi) => {
    const data = getDBSnapshot();
    await saveUserData(data);

    await thunkApi.dispatch(
      addNotification({
        text: "Данные сохранены",
        status: NotificationStatuses.success,
      })
    );
  }
);

export const updateData = createAsyncThunk("sync/updateData", (_, thunkApi) => {
  const { items = [], tags = [], periods = [], settings = {} } = getUserData();

  console.log({ items, tags, periods, settings });

  updateItemsInDb(items);
  updateTagsInDb(tags);
  updatePeriodsInDb(periods);
  updateSettingsInDb(settings);

  thunkApi.dispatch(setItems(items));
  thunkApi.dispatch(setTags(tags));
  thunkApi.dispatch(setPeriods(periods));
  thunkApi.dispatch(setSettings);
});
