import { createSlice, PayloadAction } from "@reduxjs/toolkit";

import { updateSettingsInDb } from "db";
import { TSettings } from "entities/settings/types";

type TSettingsState = {
  settings: TSettings;
};

const initialState: TSettingsState = {
  settings: {},
};

export const settingsSlice = createSlice({
  name: "settings",
  initialState,
  reducers: {
    setSettings(state, action: PayloadAction<TSettings>) {
      const settings = action.payload;
      state.settings = settings;
    },
    updateSettings(state, action: PayloadAction<Partial<TSettings>>) {
      const updatedSettings = action.payload;
      state.settings = {
        ...state.settings,
        ...updatedSettings,
      };
      updateSettingsInDb(state.settings);
    },
  },
});
