import { itemsAdapter } from "./slice";
import { createSelector } from "@reduxjs/toolkit";
import { TItem } from "entities/item/types";
import { RootState } from "shared/store";

export const {
  selectAll: selectAllItems,
  selectById: selectItemById,
  selectIds: selectItemsIds,
} = itemsAdapter.getSelectors((state: RootState) => state.items);

export const selectFlatItems = createSelector([selectAllItems], (items) => {
  const result: TItem[] = [];
  items.forEach((item) => {
    const { price, subitems = [] } = item;
    const subitemsPrice = subitems.reduce((total, i) => total + i.price, 0);
    result.push({
      ...item,
      price: price - subitemsPrice,
    });
    result.push(...subitems);
  });
  return result;
});

export const selectItemsByTag = createSelector(
  [selectFlatItems, (state: RootState, tagId?: number) => tagId],
  (items, tagId) => {
    if (!tagId) return [];
    return items.filter((item) => item.tag === tagId);
  }
);
