import { forwardRef } from "react";

import { TModalRef } from "shared/types";
import { formatDate } from "shared/utils/date";
import { Modal } from "shared/ui/modal";
import { TStatsItem } from "entities/stats/types";
import { TTag } from "entities/tag/types";

import s from "./style.module.css";

type TStatsItemsListProps = {
  tag: TTag;
  items: TStatsItem[];
};

export const StatsItemsList = forwardRef<TModalRef, TStatsItemsListProps>(
  ({ items, tag }, ref) => {
    return (
      <Modal ref={ref} title={`Траты по тегу ${tag.name}`}>
        <div className={s.Subitems}>
          {items.map((item) => (
            <div key={item.id}>
              {formatDate(item.createdAt)} - {item.total}{" "}
              {item.text && <span>({item.text})</span>}
            </div>
          ))}
        </div>
      </Modal>
    );
  }
);
