import { useSelector } from "react-redux";

import { useAppDispatch } from "shared/store";
import { TTag } from "entities/tag/types";
import { selectAllTags } from "features/tags/slice/selectors";
import { openTag } from "features/view/slice/actions";

import s from "./style.module.css";

export const TagsList = () => {
  const dispatch = useAppDispatch();
  const tags = useSelector(selectAllTags);

  const handleEdit = (tag: TTag) => {
    dispatch(openTag(tag.id));
  };

  return (
    <>
      <table className={s.List}>
        <thead>
          <tr>
            <th>Название</th>
            <th align="right">Лимит</th>
          </tr>
        </thead>
        <tbody>
          {tags.map((tag) => (
            <tr key={tag.id}>
              <td onClick={() => handleEdit(tag)}>
                <span>{tag.name}</span>
              </td>
              <td align="right" className={s.limit} onClick={() => handleEdit(tag)}>
                {tag.limit ? <span>{tag.limit}</span> : <span>&infin;</span>}
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </>
  );
};
