import { FC, useRef, useState } from "react";

import { Modal } from "shared/ui/modal";
import { TModalRef } from "shared/types";
import { TItem } from "entities/item/types";

import { ItemForm } from "../itemForm";
import { Item } from "./item";
import s from "./style.module.css";

export type TItemsListProps = {
  items: TItem[];
  disabled?: boolean;
};

export const ItemsList: FC<TItemsListProps> = ({ items, disabled }) => {
  const modalRef = useRef<TModalRef | null>(null);
  const [activeItem, setActiveItem] = useState<TItem | null>(null);

  const handleClick = (item: TItem) => {
    if (activeItem?.id !== item.id) setActiveItem(item);
    modalRef.current?.open();
  };

  const handleSubmit = () => {
    modalRef.current?.close();
    setActiveItem(null);
  };

  return (
    <>
      <div className={s.List}>
        {items.map((item) => (
          <div key={item.id} className={s.ItemWrapper}>
            <Item item={item} disabled={disabled} onClick={handleClick} />
          </div>
        ))}
      </div>
      <Modal title={activeItem ? 'Редактировать чек' : 'Новый чек'} ref={modalRef}>
        <ItemForm item={activeItem} onSubmit={handleSubmit} />
      </Modal>
    </>
  );
};
