import { forwardRef, useImperativeHandle, useRef } from "react";

import { TModalRef } from "shared/types";
import { Modal } from "shared/ui/modal";
import { TNewTagCallback, TNewTagRef } from "widgets/tags/types";
import { TagForm } from "widgets/tags/ui/tagForm";

export const NewTag = forwardRef<TNewTagRef>((_, ref) => {
  const tagModalRef = useRef<TModalRef | null>(null);
  const callbackRef = useRef<TNewTagCallback | null | undefined>(null)

  const handleSubmitTag = (tagName: string) => {
    if (callbackRef.current) callbackRef.current(tagName)
      tagModalRef.current?.close()
  }

  const handleCloseModal = () => {
    if (callbackRef.current) callbackRef.current(null)
      tagModalRef.current?.close()
  }

  useImperativeHandle(ref, () => {
    return {
      addTag: (callback) => {
        callbackRef.current = callback
        tagModalRef.current?.open()
      }
    }
  })

  return (
    <>
      <Modal title="Добавить тег" ref={tagModalRef} onClose={handleCloseModal}>
        <TagForm onSubmit={handleSubmitTag} />
      </Modal>
    </>
  );
});
